<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('InTransitItems') }}
                            </h5>
                            <button v-on:click="CloseAddAcceptRejectPopup()" type="button" class="close" data-original-title="Close">
                                <em class="fa fa-times"></em>
                            </button>

                        </div>
                        <div class="modal-body">

                            <tg-list 
                                :showCheckBox="true" 
                                :IsShowAction="false" 
                                :listType="['List']" 
                                ModuleName="Asset"
                                SubModuleCode="Asset"
                                IdentityColumn="asseT_TRANSFER_DETAILS_ID"                                                    
                                :HeaderText="$t('AssetManagement')"
                                :widgets="widgets"
                                :ListData="InTransitData" 
                                :HeaderData="Headers"                       
                                :callbackfunction="FetchData"                                
                                :LegendArray="LegendArray"
                                :SortExp="SortExp"
                                :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')"                                
                                :TotalRecords="TotalRecords"                                
                                :RenderRowActionMethod="renderActions" 
                                :ListDataCallBackFunction="FetchData"
                                v-on:PagerButtonClick="pagerMethod" 
                                v-on:HeaderButtonClick="commonHeaderButtonClick">
                                <template v-slot:slotdata="{ data }">                                   
                                    
                                    <template v-if="data.column.COLUMN_NAME == 'quantityShipped'">
                                        <span v-if="data.row.creatE_UNIQUE_NAME_LIST" class="text-dark" :title="ShippedQuantity">
                                            <i class="fa fa-list-ul" aria-hidden="true"></i>
                                            <span class="noti-circle noti-blue">{{data.row.quantityShipped }}</span>                                           
                                        </span>
                                        <span v-else class="text-dark" :title="Quantity">
                                            <i class="fa fa-list-ul" aria-hidden="true"></i>
                                            <span class="noti-circle noti-blue">{{data.row.quantity }}</span>                                           
                                        </span>                                                                                
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'quantity'">                                     
                                        <input v-if="Boolean(data.row.Quantity)" disabled="disabled" type="text" class="form-control"  v-model="data.row.quantity" />
                                         <input v-else type="text" class="form-control" disabled="disabled"  v-model="data.row.quantity" />                                                                                                                      
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'isAcceptReject'">
                                        <label class="switch medium">                                     
                                            <input type="checkbox"  :checked="data.row.isAcceptReject"  v-model="data.row.isAcceptReject" class="form-check-input rdoticketfor custom-control-input dynamic custom-switch inTransit_Accept">
                                            <span class="slider round">
                                                <span class="slider-yes">Accept</span>
                                                <span class="slider-no">Reject</span>
                                            </span> 
                                        </label>                                                                                                                    
                                    </template>                                  
                                    <template v-if="data.column.COLUMN_NAME == 'acceptRejectComment'">                                     
                                        <textarea name="comments"  onkeypress="return"  maxlength="2000" rows="3" cols="40" v-model="data.row.acceptRejectComment"></textarea>                                                                                                                     
                                    </template>
                                    <template v-if="data.column.COLUMN_NAME == 'RaiseDispute'">                                     
                                        <a href="javascript:void(0);" @click="RaiseDisputePopUp(data.row)" data-toggle-tooltip="tooltip" data-original-title="Raise Dispute">
                                            <i class="fa fa-hand-pointer-o fa-2x "></i></a>                                                                                                                   
                                    </template>
                                </template>
                         </tg-list>
                    
                        </div>

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-4 text-right">

                                    <span>
                                        <a v-if="isAcceptAll" @click="ChangeStatus($event)"
                                            class="btn btn-info formbtn text-white m-1" data-toggle-tooltip="tooltip"
                                            data-original-title="AcceptAll"><i class="fa fa-check pr-2"></i>AcceptAll</a>

                                    </span>
                                    <span>
                                        <a v-if="!isAcceptAll" @click="ChangeStatus($event)"
                                            class="btn btn-danger formbtn text-white m-1" title=""
                                            data-toggle-tooltip="tooltip" data-original-title="Reject All"><i
                                                class="fa fa-close pr-2"></i>
                                            RejectAll</a>
                                    </span>

                                    <a href="javascript:;" v-on:click="SubmitData()"
                                        class="btn btn-success formbtn text-white m-1" :title="$t('Save')"><i
                                            class="fa fa-save pr-2"></i>
                                        <span>{{ $t('Save') }}</span></a>
                                    <a href="javascript:;" v-on:click="onCancel()" :title="$t('Cancel')"
                                        class="btn btn-danger formbtn text-white m-2"><i
                                            class="fa fa-close pr-2"></i><span>{{
                                                $t('Cancel') }}</span></a>
                                </div>

                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <RaiseDisputePopUp v-if="Raisepopup" @CloseRaiseDisputePopUp="CloseRaiseDisputePopUp" :Raisedata="Raisedata">
        </RaiseDisputePopUp>
    </section>
</template>

<script>
import DataService from '../../service/DataService'

import RaiseDisputePopUp from '../Modal/RaiseDisputePopup'
export default {
    components: {
        RaiseDisputePopUp
    },
    props: {
        DetalisData: {
            type: Array,
            required: true
        },

    },
    data() {
        return {
            InTransitData: [],
            TransitRecords: '',
            IsShowPopup: false,
            assetCatelogueId: '',
            locationId: '',
            assetTransferDetailsId: '',
            assetTransferBatchId: '',
            IsAcceptReject: 1,
            isSlider: false,
            data: [],
            checkedArray: [],
            Raisepopup: false,
            Raisedata: [],
            AcceeptBtn: false,
            RejectBtn: true,
            selectAll: false,
            isAcceptAll: true,
            Headers: [
                {
                "COLUMN_NAME": "name",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ItemName"),
                "DISPLAY_ORDER": 1,
                },
                {
                "COLUMN_NAME": "uniqueName",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("UniqueName"),
                "DISPLAY_ORDER": 2,
                },
                {
                "COLUMN_NAME": "locationName",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("FromLocation"),
                "DISPLAY_ORDER": 3,
                },
                {
                "COLUMN_NAME": "itemCode",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ItemCode"),
                "DISPLAY_ORDER": 4,
                },
                {
                "COLUMN_NAME": "quantityShipped",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ShippedQuantity"),
                "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                "COLUMN_NAME": "quantity",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ReceivedQuantity"),
                "DISPLAY_ORDER": 6,
                 settings: {
                        isInSlot: true,
                    },
                },
                {
                "COLUMN_NAME": "isAcceptReject",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("AcceptRejectTicket"),
                "DISPLAY_ORDER": 7,
                 settings: {
                        isInSlot: true,
                    },
                },
                {
                "COLUMN_NAME": "acceptRejectComment",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Comments"),
                "DISPLAY_ORDER": 8,
                settings: {
                        isInSlot: true,
                    },
                },
                {
                "COLUMN_NAME": "RaiseDispute",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Action"),
                "DISPLAY_ORDER": 9,
                settings: {
                        isInSlot: true,
                    },
                },
            ],
            listheaderbuttons:[],
            LegendArray:[],            
            widgets:[],            
            noRecord:true,
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,      
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10, 
        }
    },
    async created() {

        var ths = this;
        await ths.FetchData();
    },
    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];

        },
        CloseAddAcceptRejectPopup: function () {
            this.$emit('CloseAddAcceptRejectPopup')
        },
        onCancel: function () {
            this.CloseAddAcceptRejectPopup();
        },

        FetchData: async function () {
            var vm = this;
            vm.data = [];
            vm.DetalisData.forEach(element => {
                var obj = {
                    AssetCatalogueId: element.assetCatalogueId,
                    LocationId: element.locationto,
                    AssetTransferDetailId: element.asset_transfer_deatails_Id,
                    AssetTransferBatchId: element.btach_Id,
                    CreatedAt: element.createdAt,
                }
                vm.data.push(obj);
            });    
            
            //This Get is Item not Save
            await DataService.SaveMultipleInTransitItems(vm.data).then(response => {
                vm.InTransitData = [];
                if (response.data != null) {
                    if (response.data.result.length > 0) {
                        debugger;                        
                        response.data.result.forEach(element => {
                            var obj = element;
                            obj.isSelected = false;
                            obj.ischecked=false;                            
                            obj.isAcceptReject = true;
                            obj.quantityShipped = element.quantity;
                            vm.InTransitData.push(obj);
                        })
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                    }
                    setTimeout(function () {
                    vm.CheckBoxBootstrap();
                    }, 100);
                    setTimeout(function () {
                     vm.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                }
                
            });
            
        },        

        async SubmitData() {            
            var vm = this;
            var ids = "", valids="";
            var valid=[];          
            
            $('.modal-body .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {                
                if (valids.length > 0)
                valids += ',';
                valids += $(this).val();
                valid.push($(this).val())
            });
                if (valids.length > 0) {
                    valid.forEach(ides=>{                                             
                        vm.InTransitData.forEach(ele => {
                        if (ele.asseT_TRANSFER_DETAILS_ID==ides) {                                                  
                            vm.atid = ele.asseT_TRANSFER_DETAILS_ID;
                            vm.rid = ele.relateD_INFO_ID;
                            vm.qty = ele.quantityShipped;
                            vm.rdo = ele.isAcceptReject == true ? 1 : 0;
                            // vm.rqty = ele.receviedQuantity;
                            vm.rqty = ele.quantity;
                            vm.rcom = ele.acceptRejectComment == undefined ? '' : ele.acceptRejectComment;
                            ids += vm.atid + '*' + vm.rid + '*' + vm.rqty + '*' + vm.rdo + '*' + vm.qty + '*' + vm.rcom + '|';
                        }                        
                    });
                    })           
                    var Objdata = `MoveToInTransitInfo=${ids}&assetTransferBatchId=${vm.DetalisData[0].btach_Id}`
                    await DataService.UpdateInTransitItems(Objdata).then(response => {                
                        if (response.data.result == 'success') {
                            vm.ShowAlert(response.data.msg, "success", vm.$t('Alert'));
                            vm.$parent.FetchData();
                            vm.CloseAddAcceptRejectPopup();
                            vm.isLoading = false;
                        } else {
                            vm.isLoading = false;
                            vm.ShowAlert(response.data.msg, "faliure", vm.$t('Alert'));
                        }
                    })
                }
                else {
                    vm.ShowAlert( vm.$t("Please select at least one record."), "warning",vm.$t("Alert") );
                }

        },       

        RaiseDisputePopUp(ev) {
            var vm = this;
            vm.Raisedata = ev;
            vm.Raisepopup = true;
        },

        CloseRaiseDisputePopUp: function () {
            var vm = this;
            vm.Raisepopup = false;
        },
        onCancel: function () {

            this.$emit('CloseAddAcceptRejectPopup')
        }, 
        
        pagerMethod: function (value) {           
            this.pageSize = value.pageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick:function(){
            
        },
        renderActions() {
            
        },

    }
}
</script>
<style>
    .custom-radio .custom-control-input:checked ~ .custom-control-label::after, .custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
        display: block;
    }
    .my-popups .modal-dialog {
        max-width: 1400px;
    }
    @media (max-width:1600px){
        .my-popups .modal-dialog {
                max-width: 1200px;
            }
    }
</style>
